const ModalContent = React.createClass({
  getInitialState: function () {
    return {
      currentInstance: this.props.instances[0],
      currentInboxes: this.props.instances[0].inboxes,
      currentInbox: this.props.instances[0].inboxes[0],
     // ticketDTO: {
        instance_id: this.props.instances[0].instance_id,
        staff_id: this.props.instances[0].staff_id,
        requester: this.props.requester.name,
        //requester_id: "",
        assignee: this.props.instances[0].inboxes[0].defaultAssignee,
        priority_id: this.props.priorities[0].id,
        inbox_id: this.props.instances[0].inboxes[0].id,
        subject: "",
        email: this.props.requester.email,
        description: "",
        customFields: this.getCustomFieldsFromInbox(this.props.instances[0].inboxes[0]),
        multiFilesUpload: [
          {
            data: "data:image/jpeg;base64,/9j/4AAQSkZJRgABAQEASABIAAD/2wBDAAM",
            name: 'firstFile.jpg',
            type: 'image/jpeg',
            size: 250600
          },
          {
            data: "data:image/jpeg;base64,/9j/4AAQSsdfsAQEASABIAAD/2wBDAAM",
            name: 'secondFile.jpg',
            type: 'image/jpeg',
            size: 124235
          }
        ],
        notifyCustomer: false,
     // },
      errors: {},
      isSubmitting: false
    }
  },

  getCustomFieldsFromInbox: function (inbox) {
    //debugger;
    return inbox.customFields.reduce(function(acc, val) {
      acc[val.id] = val.value;
      return acc;
    }, {});

    //console.log('cF', cF);
  },

  handleChange: function (e) {
    //debugger;
    this.setState({[e.target.name]: e.target.value });
  },

  handleCustomFieldChange: function (e) {
    //debugger;
    var updatedCF = Object.assign({}, this.state.customFields, {[e.target.name] : e.target.value});
    //this.state.customFields[e.target.name] = e.target.value;
    this.setState({customFields: updatedCF});
  },

  selectInstance: function (instance_id) {
    //console.log('instance_id', instance_id);

    var currentInstance = this.props.instances.filter((instance) => instance.instance_id == instance_id);

    //debugger;
    return currentInstance[0];
  },

  selectInbox: function (inbox_id) {
    //debugger;
    var currentInbox = this.state.currentInboxes.filter((inbox) => inbox.id == inbox_id);
    //debugger;
    return currentInbox[0];
  },

  setCurrentInstance: function (e) {

    var currentInstance = this.selectInstance(e.target.value);

    //debugger;
    this.setState({
      currentInstance : currentInstance,
      currentInboxes : currentInstance.inboxes,
      currentInbox: currentInstance.inboxes[0],
      customFields: this.getCustomFieldsFromInbox(currentInstance.inboxes[0]),
      instance_id: e.target.value
    })
  },

  setInbox: function (e) {
    let curInbox = this.selectInbox(e.target.value);

    this.setState({
      currentInbox: curInbox,
      inbox_id: e.target.value,
      customFields: this.getCustomFieldsFromInbox(curInbox)
    })
  },

  componentDidMount: function () {
    //console.log('MOUNTEDDDDDD', $.fn.jquery);
    jQuery(this.refs['modal-body']).scrollbar();
    jQuery(".btn").corner('15px');
  },

  handleCreate: function (e) {
    e.preventDefault();

    this.setState({
      isSubmitting: true
    }, function () {

      console.log('Handling CLICK ...');
      // debugger;
      if (Object.keys(this.state.errors).length === 0) {
        console.log('form is valid');

          $.ajax({
            method: 'POST',
            url: 'api/ticket',
            dataType: 'json',
            data: {
              instance_id: this.state.instance_id,
              staff_id: this.state.currentInstance.staff_id,
              requester: this.state.requester,
              //requester_id: "",
              assignee: this.state.assignee,
              priority_id: this.state.priority_id,
              inbox_id: this.state.inbox_id,
              subject: this.state.subject,
              email: this.state.email,
              description: this.state.description,
              customFields: this.state.customFields
            }
          });


      } else {
        console.log('form is not valid');
/*        this.setState({
          isSubmitting: false
        })*/
      }

    });

  },

  onFormSubmit: function (e) {
    //e.preventDefault();

    this.setState({
      isSubmitting: true
    }, function () {

      console.log('Submitting form ...');
      // debugger;
      if (Object.keys(this.state.errors).length === 0) {
        console.log('form is valid');


      } else {
        console.log('form is not valid');
        /*        this.setState({
         isSubmitting: false
         })*/
      }

    });
  },

  render: function () {
    return (
      <form name="createTicket" onSubmit={this.onFormSubmit} encType="multipart/form-data">
        <div ref="modal-body" className="modal-body scrollbar-macosx scrollbar-popup">
          <div className="row nomargin">
            <div className="col-md-12">
              <div className="row">
                <div className="col-xs-6" >
                  <div className="form-group">
                    <label>Requester</label>
                    <p><b>{this.props.requester.name}</b></p>
                  </div>
                </div>
                <SelectField onChange={this.setCurrentInstance}
                             label={this.props.labels.instance}
                             options={this.props.instances}
                             name="instance_id"
                             optionValue="instance_id"
                             optionName="instance_url" />
              </div>
              <div className="row">
                <SelectField onChange={this.setInbox}
                             label={this.props.labels.inbox}
                             options={this.state.currentInboxes}
                             name="inbox_id"
                />
                <SelectField label={this.props.labels.assignee}
                             onChange={this.handleChange}
                             options={this.state.currentInbox.assigneeList}
                             name="assignee"
                             selectedDefault={this.state.currentInbox.defaultAssignee}
                />
                <SelectField label={this.props.labels.priority}
                             onChange={this.handleChange}
                             options={this.props.priorities}
                             name="priority_id"
                />
                <CustomFieldsContainer onChange={this.handleCustomFieldChange} isSubmitting={this.state.isSubmitting} errors={this.state.errors} fields={this.state.currentInbox.customFields} />
                <ModalContentFooter onChange={this.handleChange} labels={this.props.labels} isSubmitting={this.state.isSubmitting} errors={this.state.errors} />
              </div>
            </div>
          </div>
        </div>
        <ModalFooterButtons onCreate={this.handleCreate} />
      </form>
    );
  }
});

module.exports = ModalContent;



