var ModalContentFooter = React.createClass({

  getInitialState: function() {
    return {
      isSendEmailChecked: false
    };
  },

  handleAttach: function (e) {
      this.refs['file-input'].click();
     // console.log('handling AttachClick');
     // console.log('file-input files', this.refs['file-input'].files, e.target);


/*      if ('FileReader' in window) {
        //debugger;
        let reader = new FileReader();
        var file = this.refs['file-input'].files[0];

        reader.readAsDataURL(file);

        reader.onloadend = function () {
          //debugger;
          this
        };


      } else {
        console.log('Using Polyfill for FileReader');
      }*/
  },

  handleFile: function (e) {
    console.log('handling file');
    console.log("e.target.value", e.target.value);
  },

  handleCheckClick: function (e) {
      console.log('handling CheckClick');
      this.refs['checkbox-input'].click();

      this.setState({
        isSendEmailChecked: !this.state.isSendEmailChecked
      });
  },

  render: function () {

    var checkBoxClass = "checkbox-icon " + ((this.state.isSendEmailChecked) ? 'checked' : 'unchecked');



    return (
      <div>

        {/*<div className="col-md-12">
          <div className="form-group">
            <label htmlFor="subject">Subject</label><span>*</span>
            <input type="text" id="subject" required name="subject" className="form-control" />
          </div>
        </div>*/}

        <InputField name="subject" onChange={this.props.onChange} size="md" isSubmitting={this.props.isSubmitting} errors={this.props.errors} label={this.props.labels.subject} required={true} />
        {/*<div className="col-md-12">
          <div className="form-group">
            <label htmlFor="desctiptionTextarea">Description</label><span>*</span>
            <textarea id="desctiptionTextarea" name="description" required className="form-control desctiption-textarea"></textarea>
          </div>
        </div>*/}

        <InputField name="description" onChange={this.props.onChange} size="md" type="textarea" isSubmitting={this.props.isSubmitting} errors={this.props.errors} label={this.props.labels.description} required={true} />


        <InputFileContainer />

        <div className="col-md-12">
            <div id="check-tick" onClick={this.handleCheckClick} className={checkBoxClass}></div>
            <input name="notifyCustomer" ref="checkbox-input" className="checkbox-input" type="checkbox" />
            <label onClick={this.handleCheckClick}>Send Email Notification to Client</label>
        </div>


      </div>
    );
  }
});

module.exports = ModalContentFooter;



