var React = require('react');
const ModalHeader = require('./ModalHeader');
const ModalContent = require('./ModalContent');






function ModalBox ({title, instances, requester, priorities, labels}) {

  //console.log('props', title);
  return (
    <div className="modal-dialog modal-450">
      <div className="modal-content">
        <div id="ticket-create" className="popup ticket-create">
          <ModalHeader title={title} />
          <ModalContent labels={labels} instances={instances} requester={requester} priorities={priorities} />
        </div>
      </div>
    </div>
  );
}

module.exports = ModalBox;



